.cart-dropdown-container {
  display: flex;
  flex-direction: column;
  row-gap: 10px;

  position: fixed;

  width: 472px;
  max-height: 100vh;
  height: calc(100vh - 80px);

  padding: 20px 45px;

  top: 80px;
  right: 0;

  background-color: #fff;

  box-shadow: 0 0 10px rgb(0 0 0 / 10%);

  .cart-close-button {
    position: absolute;

    top: 10px;
    left: 10px;

    width: 15px;
    height: 15px;
    cursor: pointer;

    border-radius: 0;
    border: none;
    transition: 0.1s;

    color: #ea1d2c;
    background-color: #fff;

    .cart-dropdown-close-button {
      height: 100%;
      width: 100%;
    }
  }

  .cart-dropdown-header {
    display: flex;
    flex-direction: column;
    row-gap: 10px;

    padding-bottom: 20px;

    .cart-header-title {
      color: #717171;
      font-weight: 300;
      font-size: 1.4rem;
      width: 100%;
    }
    .cart-header-restaurant {
      font-size: 1.8rem;
      font-weight: 600;
      color: inherit;
      //max-width: 70%;
    }
  }

  .cart-items {
    border-bottom: 1px solid #dcdcdc;
    .cart-section {
      .cart-section-title {
        font-size: 1.4rem;
        font-weight: 600;

        padding-bottom: 10px;
      }
    }
  }

  .cart-footer {
    display: flex;
    flex-direction: column;

    gap: 10px;

    padding: 20px 0px;

    .cart-footer-subtotal {
      display: flex;
      justify-content: space-between;

      .cart-footer-subtotal-title {
        font-size: 1.4rem;
        color: #717171;
      }

      .cart-footer-subtotal-price {
        font-size: 1.6rem;
        color: #3f3e3e;
      }
    }
    .cart-footer-delivery {
      display: flex;
      flex-direction: column;
      .cart-footer-delivery-info {
        display: flex;
        justify-content: space-between;

        .cart-footer-delivery-title {
          font-size: 1.4rem;
          color: #717171;
        }

        .cart-footer-delivery-price {
          font-size: 1.6rem;
          color: #50a773;
        }
      }

      .cart-footer-delivery-content {
        display: flex;
        flex-direction: column;
        gap: 10px;

        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;

        padding: 10px 45px;
        // min-height: 100px;
        // background-color: #fff;

        .cart-footer-total {
          display: flex;
          justify-content: space-between;

          .cart-footer-total-title {
            font-size: 1.8rem;
            font-weight: 600;
            color: #3f3e3e;
          }
          .cart-footer-total-price {
            font-size: 1.6rem;
            font-weight: 600;
            color: #3f3e3e;
          }
        }

        .cart-footer-button {
          text-decoration: none;
          border: none;
          padding: 0 20px;
          border-radius: 4px;
          margin: 3px 0;
          height: 50px;
          font-weight: 500;
          transition: 0.1s;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          position: relative;
          overflow: hidden;

          width: 100%;
          background: #ea1d2c;
          color: #fff;
          cursor: pointer;

          .cart-footer-button-info {
            font-size: 1.6rem;
            font-weight: 600;
            text-align: center;
          }
        }
      }
    }
  }

  .cart-dropdown-empty {
    display: flex;
    flex-flow: column nowrap;
    row-gap: 32px;
    justify-content: center;
    text-align: center;
    height: 100%;

    color: #dcdcdc;

    .cart-dropdown-empty-img {
      height: 270px;
      width: auto;

      .cart-dropdown-empty {
        width: 100%;
        height: 100%;
        color: #ea1d2c;
      }
    }

    .cart-dropdown-empty-title {
      font-size: 1.6rem;
      color: #3e3e3e;
      font-weight: 600;
    }

    .cart-dropdown-empty-content {
      font-size: 1.4rem;
      font-weight: 500;
      color: #717171;
    }
  }
}

